import { Injectable } from '@angular/core';
import { PaginationMode } from './pagination.type';

@Injectable()
export class PaginationConfig {
    /**
     *  `button`,`pointer``button`
     */
    mode: PaginationMode = 'button';
    /**
     * `false`
     */
    simple: boolean = false;

    /**
     * `true`
     */
    mini: boolean = true;

    /**
     * HTML``
     */
    prevText: string = '';
    /**
     * HTML``
     */
    nextText: string = '';
}



