import { Injectable } from '@angular/core';
import { ButtonType } from '../utils/types';

@Injectable()
export class ButtonConfig {

    /**
     * primarydefaultwarn
     */
    type: ButtonType = 'primary';

    /**
     * disabled`false`
     */
    disabled: boolean = false;

    /**
     * `false`
     */
    loading: boolean = false;

    /**
     * `false`
     */
    plain: boolean = false;

    /**
     * `false`
     */
    mini: boolean = false;
}



