import { Injectable } from '@angular/core';

@Injectable()
export class PopupConfig {
    /**
     * `false`
     */
    is_full?: boolean = false;

    /**
     * ``
     */
    cancel?: string = '';

    /**
     * ``
     */
    confirm?: string = '';

    /**
     * `true`
     */
    backdrop?: boolean = true;
}



