import { Injectable } from '@angular/core';

export type PositionType = 'left' | 'right' | 'top' | 'bottom';

export type ModeType = 'over' | 'slide';

@Injectable()
export class SidebarConfig {
    /**
     * `left`
     */
    position: PositionType = 'left';

    /**
     * `slide`
     * - over: 
     * - slide
     */
    mode: ModeType = 'slide';

    /**
     * `true`
     */
    backdrop: boolean = true;
}



