import { Directive, Input, Renderer, ElementRef, OnInit, OnChanges, SimpleChanges, forwardRef } from '@angular/core';
import { Validator, AbstractControl, Validators, NG_VALIDATORS, ValidatorFn, ValidationErrors } from '@angular/forms';
import { findParent, add, remove } from './../utils/dom';

/**
 * 
 */
@Directive({
    selector: '[weui-input]',
    providers: [{
        provide: NG_VALIDATORS,
        useExisting: forwardRef(() => InputDirective),
        multi: true
    }]
})
export class InputDirective implements OnInit, OnChanges, Validator {

    private parentEl: any;
    private ftEl: any;
    private pattern: RegExp;
    private _validator: ValidatorFn;
    private _onChange: () => void;

    /**
     * **** <intpu type="text"> 
     *  weui-input 
     * number/digit()/qq/email/tel/mobile/idcard weui-regex
     */
    @Input('weui-input') inputType: string;
    /**
     *  [weui-input]
     */
    @Input('weui-regex') inputRegex: RegExp | string;

    /**
     * **** <intpu required> 
     */
    @Input('weui-required') required: 'info' | 'warn' | 'waiting' = 'warn';

    /**
     * 
     */
    @Input('weui-cleaner') cleaner: boolean = false;

    constructor(private el: ElementRef) { }

    ngOnInit() {
        this.parentEl = findParent(this.el.nativeElement, '.weui-cell');
        if (!this.parentEl) throw new Error('DOM.weui-cell');
        //  weui-cell__ft
        this.ftEl = add(this.parentEl);
    }

    ngOnChanges(changes: SimpleChanges): void {
        this._createValidator();
        if (this._onChange) this._onChange();
    }

    private _createValidator(): void {
        let regex: RegExp = null;
        if (this.inputRegex) {
            if (typeof this.inputRegex === 'string') {
                regex = new RegExp(`^${this.inputRegex}$`);
            } else {
                regex = this.inputRegex;
            }
        } else {
            // 
            if (this.inputType) {
                switch (this.inputType) {
                    case 'qq':
                    case 'number':
                        regex = /^[0-9]+$/;
                        break;
                    case 'digit':
                        regex = /^[.0-9]+$/;
                        break;
                    case 'tel':
                        regex = /^[-.0-9]+$/;
                        break;
                    case 'email':
                        regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                        this.cleaner = true;
                        break;
                    case 'mobile':
                        regex = /^1[0-9]{10}$/;
                        this.cleaner = true;
                        break;
                    case 'idcard': // 
                        regex = /^[X0-9]{15,18}$/;
                        this.cleaner = true;
                        break;
                }
            }
        }

        this._validator = (control: AbstractControl): ValidationErrors | null => {
            let value: string = control.value;
            if (value == null || value.length === 0) {
                if (this.required !== undefined)
                    return { 'icon': this.required, 'type': 'required', 'actualValue': value };

                return null;
            }
            if (this.cleaner && value.includes(' ')) {
                value = value.replace(/ /g, '');
                control.setValue(value);
            }
            return regex === null || regex.test(value) ? null : { 'icon': 'warn', 'type': 'regex', 'actualValue': value };
        };
    }

    registerOnValidatorChange(fn: () => void): void { this._onChange = fn; }
    validate(c: AbstractControl): ValidationErrors | null {
        const ret = this._validator(c);
        if (ret === null) {
            this.parentEl.classList.remove('weui-cell_warn');
            remove(this.ftEl, 'i');
        } else {
            remove(this.ftEl, 'i');
            this.parentEl.classList.add('weui-cell_warn');
            const icon = `weui-icon-${ret.icon}`;
            add(this.ftEl, '.' + icon, 'i', icon);
        }
        return ret;
    }
}



