import { Injectable } from '@angular/core';

@Injectable()
export class LoadmoreConfig {
    /**
     * `loading`
     */
    type?: 'loading' | 'line' | 'dot' = 'loading';

    /**
     * type=='loading'``
     */
    loadingText?: string = '';

    /**
     * type=='line'``
     */
    lineText?: string = '';
}



