import { Injectable } from '@angular/core';
import { SkinType } from '../utils/types';

@Injectable()
export class ActionSheetConfig {
    /**
     * `ios`
     */
    skin?: SkinType = 'ios';

    /**
     * 
     */
    title?: string;

    /**
     * ``
     */
    cancel?: string = '';

    /**
     * `true`
     */
    backdrop?: boolean = true;
}



