import { Injectable } from '@angular/core';

@Injectable()
export class InfiniteLoaderConfig {
    /**
     * `100vh`
     */
    height?: string = '100vh';

    /**
     * x%75%
     */
    percent?: number = 75;

    /**
     * HTMLweui-loadmore 
     */
    loading?: string = '<div class="weui-loadmore"><i class="weui-loading"></i><span class="weui-loadmore__tips"></span></div>';

    /**
     * HTMLweui-loadmore 
     */
    finished?: string = '<div class="weui-loadmore weui-loadmore_line"><span class="weui-loadmore__tips"></span></div>';

    /**ms`100` */
    throttle?: number = 100;
}



