import { Injectable } from '@angular/core';
import { AnimateType } from '../utils/types';

@Injectable()
export class AccordionConfig {
    /**
     * `false` `true` `false`
     */
    collapsible: boolean = false;
    /**
     * `true`
     */
    activeFirst: boolean = true;
    /**
     * `none` `slide` `slide`
     */
    animate: AnimateType = 'slide';
}



