import { Injectable } from '@angular/core';

@Injectable()
export class RatingConfig {
    /**
     * 5
     */
    max?: number = 5;

    /**
     * 
     */
    cls?: string = '';

    /**
     * `weui-icon-circle`
     */
    stateOff?: string = 'weui-icon-circle';

    /**
     * `weui-icon-success`
     */
    stateOn?: string = 'weui-icon-success';

    /**
     *  `max``stateOff``stateOn` 
     */
    states?: { on: string, off: string }[] = [];

    /**
     *  `title`  `1` 
     */
    titles?: string[] = [];
}



