import { Injectable } from '@angular/core';

@Injectable()
export class UploaderConfig {

    /**
     * 
     */
    url?: string;

    /**
     * HTTP
     */
    method?: 'POST' | 'GET';

    /**
     * name
     */
    alias?: string;

    /**
     * `true`
     */
    withCredentials?: boolean = true;

    /**
     * headers 
     */
    headers?: { [key: string]: any };

    /**
     * `false`
     *  true  `upload`
     */
    auto?: boolean = false;

    /**
     * mime[ '' ]
     */
    mimes?: string[];

    /**
     * [ 'jpg', 'png' ]
     */
    types?: string[];

    /**
     * -1 `-1`
     */
    limit?: number = -1;

    /**
     * -1 `-1`
     */
    size?: number = -1;
}



