import { Injectable } from '@angular/core';
import { SkinType, ButtonType, InputType, InputData } from '../utils/types';

@Injectable()
export class DialogConfig {

    /**
     * `default`
     * defaultHTML
     * prompt
     */
    type?: 'default' | 'prompt' = 'default';

    /**
     * `auto`
     */
    skin?: SkinType = 'auto';

    /**
     * 
     */
    title?: string;

    /**
     * HTML
     */
    content?: string;

    /**
     * Input `type` 
     */
    input?: InputType;
    /**
     * Input `placeholder` 
     */
    inputPlaceholder?: string;
    /**
     * Input 
     */
    inputValue?: any;
    /**
     * Input `required`  `disabled` `true`
     */
    inputRequired?: boolean;
    /**
     * Input 
     *  `input` `email``url` 
     */
    inputRegex?: RegExp;
    /**
     * 
     */
    inputError?: string;

    /**
     * input `select` `radio` `checkbox` 
     */
    inputOptions?: InputData[];

    /**
     * HTML `min` `max` 
     */
    inputAttributes?: any;

    /**
     * false``
     */
    cancel?: string = '';

    /**
     * `default`
     */
    cancelType?: ButtonType = 'default';

    /**
     * true``
     */
    confirm?: string = '';

    /**
     * `primary`
     */
    confirmType?: ButtonType = 'primary';

    /**
     *  `cancel` & `confirm` 
     */
    btns?: { text: string, type: ButtonType, value: boolean | any, [key: string]: any }[];

    /**
     * `false`
     */
    backdrop?: boolean = false;
}



